import React, { useState } from 'react';
import NavButton from '../shared/components/NavButton';

export default function Header({ section, setSection, theme, setTheme }) {
  const [spinning, setSpinning] = useState(false);
  return (
    <header className="header">
      <div className="container">
        <h1 className="logo">GameTracker</h1>
        <div className="nav-container">
          <nav className="nav">
            <NavButton active={section === 'biblioteca'} onClick={() => setSection('biblioteca')}>Biblioteca</NavButton>
            <NavButton active={section === 'agregar'} onClick={() => setSection('agregar')}>Agregar Juego</NavButton>
            <NavButton active={section === 'reseas'} onClick={() => setSection('reseas')}>Reseas</NavButton>
            <NavButton active={section === 'estadisticas'} onClick={() => setSection('estadisticas')}>Estadsticas</NavButton>
          </nav>
          <button
            className={`theme-toggle ${spinning ? 'rotating' : ''}`}
            title="Cambiar tema"
            onClick={() => {
              setSpinning(true);
              setTheme((t) => (t === 'light' ? 'dark' : 'light'));
              setTimeout(() => setSpinning(false), 600);
            }}
          >
            {theme === 'light' ? (
              <svg className="icon" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <circle cx="12" cy="12" r="5" />
                <line x1="12" y1="1" x2="12" y2="3" />
                <line x1="12" y1="21" x2="12" y2="23" />
                <line x1="4.22" y1="4.22" x2="5.64" y2="5.64" />
                <line x1="18.36" y1="18.36" x2="19.78" y2="19.78" />
                <line x1="1" y1="12" x2="3" y2="12" />
                <line x1="21" y1="12" x2="23" y2="12" />
                <line x1="4.22" y1="19.78" x2="5.64" y2="18.36" />
                <line x1="18.36" y1="5.64" x2="19.78" y2="4.22" />
              </svg>
            ) : (
              <svg className="icon" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <path d="M21 12.79A9 9 0 1 1 11.21 3 7 7 0 0 0 21 12.79z" />
              </svg>
            )}
          </button>
        </div>
      </div>
    </header>
  );
}